import * as React from 'react';
import styled from 'styled-components';
import SliderItem from './slider-item';

const SliderWrapper = ({items, setSlide, currentSlide}) => {
    const containerRef = React.useRef(null);  
  
    const handleSetSlide = React.useCallback((id) => {
      setSlide(id);
    },[setSlide]); 
    
    const handleIntersection = React.useCallback((entries) => {
      const [entry] = entries;
      const activeSlide = Number(entry.target.dataset.slide);
      if (!entry.isIntersecting || activeSlide === "NaN") return;
  
      handleSetSlide(activeSlide, handleSetSlide);
    }, [handleSetSlide]);
    
    React.useEffect(() => {
      const observer = new IntersectionObserver(
        handleIntersection,
        {
          root: containerRef.current,
          threshold: 0.45
        }
      );
      Array.from(containerRef.current.children).forEach((item) => {
          observer.observe(item);
      });
      return function() {
         observer.disconnect();
      }
    }, [items, handleIntersection]);
    
    return (
          <StyledSliderWrapper ref={containerRef} >
              {items.map((item, index) => {
                  return <SliderItem key={index} index={index} image={item.localFile.childImageSharp.gatsbyImageData} isActive={currentSlide === index} />
                  })}
          </StyledSliderWrapper>
    )
  };
  
  const StyledSliderWrapper = styled.div`
      position: absolute;
      display: flex;
      width: 100%;
      height: 100%;
      overflow-x: auto;
      scroll-snap-type: x mandatory;
      scroll-behavior: smooth; 
      -ms-overflow-style: none;
      scrollbar-width: none;
  
      &::-webkit-scrollbar {
          display: none;
        }
  `;
  
  export default SliderWrapper;