import React from "react";
import { graphql } from 'gatsby';

import Page from "../../components/layout/page";
import { LayoutContainer, LayoutItemWrapper, LayoutRow } from "../../components/layout/layout";
import { TextLink } from "../../components/base/links";
import { Heading } from "../../components/base/typography";
import MarkdownWrapper from "../../components/markdown/markdown-wrapper";
import Tag from "../../components/base/tag";
import Slider from "../../components/slider/slider";
import { ButtonLink } from "../../components/base/links";

export default function WorkDetail ({data}) {

  return (
    <>
      <Page 
        active="work"
        // title={data.strapiPost.SEO.metaTitle}
        // description={data.strapiPost.SEO.metaDescription}
        // keyword={data.strapiPost.SEO.keywords}
        // image={data.strapiPost.cover}
      >
        <LayoutContainer>   
          <LayoutRow>
            <LayoutItemWrapper center>
                <Slider images={data.strapiWork.workphotos} />
            </LayoutItemWrapper>
            <LayoutItemWrapper rowGap columnCenter>
                  <div>
                <TextLink to="/work"><i class="fa-solid fa-chevron-left" />Back to overview</TextLink>
                  </div>
                  <div>
                    <Heading level={2}>{data.strapiWork.name}</Heading>
                    <Tag>{data.strapiWork.category.name}</Tag>
                  </div>
                  <MarkdownWrapper
                    options={{
                      p: {
                        topMargin: false
                      }
                    }}>
                    {data.strapiWork.description.data.childMdx.body} 
                  </MarkdownWrapper>
                  <div>
                    <ButtonLink href={data.strapiWork.demoUrl} as="a">View demo</ButtonLink>
                  </div> 
            </LayoutItemWrapper>
          </LayoutRow>   
          </LayoutContainer>
      </Page>
    </>
  )
};

export const query = graphql`
query getWorkItem($slug: String) {
    strapiWork(slug: {eq: $slug}) {
      id
      name
      description {
        data {
          childMdx {
            body
          }
        }
      }
      demoUrl
      category {
        name
      }
      workphotos {
        localFile {
          childImageSharp {
            gatsbyImageData
          }
        }
      }
    }
  }  
`