import * as React from 'react';
import styled from 'styled-components';

const SliderNav = ({currentSlide, maxItems, moveToSlide}) => {
  const moveSlideUp = () => {
    moveToSlide(currentSlide + 1);
  }
  
  const moveSlideDown = () => {
    moveToSlide(currentSlide - 1);
  }
  
  return (
    <StyledSliderNav>
      <StyledSliderNavButton onClick={moveSlideDown} disabled={currentSlide === 0}>
        <i class="fa-solid fa-chevron-left" /> 
      </StyledSliderNavButton>
      <StyledSliderNavIndicator>
        {currentSlide + 1} / {maxItems}
      </StyledSliderNavIndicator>
      <StyledSliderNavButton onClick={moveSlideUp} disabled={currentSlide + 1 === maxItems}>
        <i class="fa-solid fa-chevron-right" /> 
      </StyledSliderNavButton>
    </StyledSliderNav>
  )
};

const StyledSliderNav = styled.div`
    position: absolute;
    display: flex;
    right: 0;
    bottom: 0;
    background: var(--color-neutral-0);
    color: white;
`;

const StyledSliderNavIndicator = styled.div`
    display: flex;
    justify-content: center;
    align-items: center;
    min-width: 4.5rem;
    padding: 0 1rem;
    color: var(--color-text);
    font-weight: bold;
    box-sizing: border-box;
`;

const StyledSliderNavButton = styled.button`
    height: 4rem;
    width: 4rem;
    background-color: var(--color-neutral-0);
    color: var(--color-text);
    font-size: 1.5rem;
    outline: none;
    border: none;
    border-radius: 0;
    cursor: pointer;
    transition: all 0.2s ease; 

    &:hover, &:focus {
        background-color: var(--color-neutral-3);
    }

    &:disabled {
      background-color: var(--color-neutral-0);
      color: var(--color-neutral-0);
      pointer-events: none;
    } 
`;

export default SliderNav;